body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/*Animations*/
.green_one {
  -webkit-animation: green_one linear 3s 1 both;
  animation: green_one linear 3s 1 both;
}
.green_two {
  -webkit-animation: green_two linear 3s 1 both;
  animation: green_two linear 3s 1 both;
}
.green_three {
  -webkit-animation: green_three linear 3s 1 both;
  animation: green_three linear 3s 1 both;
}
.green_four {
  -webkit-animation: green_four linear 3s 1 both;
  animation: green_four linear 3s 1 both;
}
.green_five {
  -webkit-animation: green_five linear 3s 1 both;
  animation: green_five linear 3s 1 both;
}
.green_six {
  -webkit-animation: green_six linear 3s 1 both;
  animation: green_six linear 3s 1 both;
}
.electric_on {
  -webkit-animation: electric_on ease-in 3s 1 both;
  animation: electric_on ease-in 3s 1 both;
}
.electric_on_body {
  -webkit-animation: electric_on_body ease-in 3s 1 both;
  animation: electric_on_body ease-in 3s 1 both;
}
.electric_on_eye {
  -webkit-animation: electric_on_eye ease-in 1s 1 both;
  animation: electric_on_eye ease-in 1s 1 both;
}

.fade-in-first {
  -webkit-animation: fade-in-first ease-in 5s 1 both;
  animation: fade-in-first ease-in 5s 1 both;
}
.fade-in-fifth {
  -webkit-animation: fade-in-fifth ease-in 3s 1 both;
  animation: fade-in-fifth ease-in 3s 1 both;
}
.fade-in-fourth {
  -webkit-animation: fade-in-fourth ease-in 3s 1 both;
  animation: fade-in-fourth ease-in 3s 1 both;
}
