@-webkit-keyframes leaf-twitch {
  0% {
    
    transform: none;
   }
  74%{
    transform: none;
  }
   75%{
 
     transform: rotate3d(1, 1, 1, 15deg);
   }
   80%{
     transform: none;
   }
   100% {
     
    transform: none;
   }
}

@keyframes leaf-twitch {
  0% {
    
    transform: none;
   }
  74%{
    transform: none;
  }
   75%{
 
    transform: rotate3d(1, 1, 1, 15deg);
   }
   80%{
     transform: none;
   }
   100% {
     
    transform: none;
   }
}

